import { provide, inject, reactive, createApp, h } from 'vue';
const ToastSymbol = Symbol();

const globalConfig = {
    type: 'bottom', // toast
    duration: 2500, // 
    wordWrap: false, // 
    width: 'auto' // 
};

const state = reactive({
    show: false, // toast
    text: ''
});

let [toastTimer, toastVM, toastWrapper] = [null, null, null];

const _toast = text => {
    state.show = true;
    state.text = text;
    if (!toastVM) {
        // toast
        toastVM = createApp({
            setup() {
                return () =>
                    h(
                        'div',
                        {
                            // 
                            class: [
                                'lx-toast animate__animated animate__bounceIn'
                            ],
                            style: {
                                display: state.show ? 'block' : 'none',
                                width: globalConfig.width
                            }
                        },
                        state.text
                    );
            }
        });
    }

    if (!toastWrapper) {
        // 
        toastWrapper = document.createElement('div');
        toastWrapper.id = 'lx-toast';
        document.body.appendChild(toastWrapper);
        toastVM.mount('#lx-toast');
    }
    if (toastTimer) clearTimeout(toastTimer);
    // 
    toastTimer = setTimeout(() => {
        state.show = false;
        clearTimeout(toastTimer);
    }, globalConfig.duration);
};

export function provideToast(config = {}) {
    for (const key in config) {
        globalConfig[key] = config[key];
    }
    provide(ToastSymbol, _toast);
}

export function useToast(config = {}) {
    for (const key in config) {
        globalConfig[key] = config[key];
    }
    const toast = inject(ToastSymbol,_toast);
    if (!toast) {
        throw new Error('error');
    }
    return toast;
}